#contenedor{
    display: grid;
   
    grid-template-columns: 100%;

    font-family:Georgia, 'Times New Roman', Times, serif;
    color: rgb(228, 223, 236);
    font-size:16px;

    
    background-image: linear-gradient(to right,  rgb(179, 52, 168), rgb(109, 55, 209));
}



#row{
    height: 100vh;
    width: 100vw;
    background-image: linear-gradient(to right,  rgb(179, 52, 168), rgb(109, 55, 209));
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    justify-content: space-around;
    
}
nav{
    height:10%;
    width: 60%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    

}
input{
    outline: none;
    border: none;
    border-bottom: 1px solid whitesmoke;
    background-color: rgb(146, 103, 182);
    margin-right: 10px;
    
}
::placeholder {
    color: whitesmoke;
    
}

button{
    background-color: rgba(221, 216, 231, 0.3);
    outline: none;
    border: none;
    border-radius: 10px;
    padding: 5px;
}
button:focus{
    border: none;
    outline: none;
    background-color: rgb(110, 40, 128);
}

#display{
    border: 2px solid blanchedalmond;
    height:50%;
    width: 60%;
    border-radius: 10px;


    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(10rem,1fr));
   grid-gap: 5px;
   overflow: auto;
    

}

